<template>
	<div class="container flex row" @click="click">
		<div class="body flex column">
			<text class="title">{{ item.title }}</text>
			<text class="content">{{ item.description }}</text>
			<text class="footer-text">{{ item.completeCount }} 人参与｜共 {{ item.content.length }} 问｜耗时 {{ item.estTime/60 }} 分钟 </text>
		</div>
		<view class="cover-container">
			<image :src="item.cover" class="cover flex flex-one" />
		</view>
	</div>
</template>

<script>
	import tag from "@/components/tag.vue";
	import moment from 'moment';
	import { AUTHOR_TYPE } from "@/common/constants.js";
	
	export default {
		components: { tag },
		props: {
			item: { type: Object, default: () => {} }
		},
		data() {
			return { };
		},
		computed: {
			updatedAtRelative: function() {
				// moment.locale('zh_CN');
				// return moment(this.item.updatedAt).fromNow().replace(/\s/g, '');
				return moment(this.item.updatedAt).format("YYYY-MM-DD");
			},
		},
		methods: {
			getFullTitle(author, isDoctor) {
				let { name, title, organization } = author;
				const organizationName = organization && author.organization.name ? author.organization.name : '';
				name = name ? name : '';
				title = title ? title : '';
				
				return `${name}${isDoctor ? ' ' : ''}${title} ${organizationName ? (' | ' + organizationName) : ''}`;
			},
			share(event) {
				if (event) {
					event.preventDefault()
				}
			},
			click() {
				this.$emit('click', this.item);
			}
		}
	}
</script>

